.todoField{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}
.goBack{
    position: absolute;
    left: 0;
    top: 0;
}
.form{
    border: 1px solid gray;
    min-height: 350px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px 20px;
    border-radius: 30px;
    width: 330px;
}
.form div{
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 100%;
}
.form div input{
    width: 100%;
    height: 34px;
    border-radius: 32px;
    border: 1px solid gray;
    padding-left: 15px;
    font-size: 16px;
}
.form div span{
    color: red;
}
.form div input:focus{
    outline: none;
}
.form button{
    background: #0000ff;
    width: 150px;
    height: 32px;
    border-radius: 32px;
    border: none;
    color: white;
    font-size: 16px;
    margin-top: 30px;
    cursor: pointer;
}
button a{
    color: white;
}