li{
  list-style: none;
  display: flex;
  justify-content: space-between;
  border: 1px solid darkgray;
  height: 50px;
  align-items: center;
  padding: 0 30px;
  border-top: none;
}
li:nth-child(1){
  border-top: 1px solid darkgray;
}

.editIcon{
  fill: #4a4ad9;
  cursor: pointer;
}
.deleteIcon{
  fill: red;
  cursor: pointer;
}
.table{
  width: 100%;
}
.table thead{
  width: 100%;
  background: #e0e0e0;
  height: 20px;
}
.table tbody{
  width: 100%;
  border: 1px solid;
}
.table tbody tr td{
  padding: 0 20px;
}
.table .tbody tr{
  border: 1px black solid;
}
.icons{
  display: flex;
  gap: 0 20px;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.top{
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
a{
  color: #4a4ad9;
  text-decoration: none;
}
